import {
  GET_PATIENTINFO,
  SET_USERINFO
} from './mutation_types.js';
import common, { ajaxAsync } from '@/util/common';


export default {
  /**/
  async getPatientInfo({
    commit,
    state
  }, obj) {
    let res = await ajaxAsync({
      url: "baseinfo2.0/inpatients/inpatients",
      dataType: "json",
      data: obj,
      type: "get"
    });
    if (res && res.length > 0) {
      commit(GET_PATIENTINFO, res);
    } else {
      common.openLayer("");
    }
  },

   /**/
  async setEmcPatientInfo({
    commit,
    state
  }, obj) {
    let res = await ajaxAsync({
      url: "baseinfo2.0/emc/patinet",
      dataType: "json",
      data: obj,
      type: "get"
    });
    if (res && res.length > 0) {
      commit(GET_PATIENTINFO, res);
    } else {
      common.openLayer("");
    }
  },

  /**/
  async setUserInfo({
    commit,
    state
  }, obj) {
    let res = await ajaxAsync({
      url: "user-service/userdetail",
      dataType: "json",
      type: "get"
    });
    if (res) {
      commit(SET_USERINFO, res);
    } else {
      common.openLayer("");
    }
  },


    /**/
  async getUnitInfo({
    commit,
    state
  }, obj) {
    let res = await ajaxAsync({
      url: "sys-service/sys/v1/units/withVirtual",
      dataType: "json",
      data: obj,
      type: "get"
    });
    if (res) {
      commit(SET_USERINFO, res);
    } else {
      common.openLayer("");
    }
  },
}



